
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import {mapGetters, mapMutations} from 'vuex'
import {areaList, cityList, provinceList} from 'base/areaData'
import {areajson} from 'base/area.js'

export default {
mounted() {
for (let pro of areajson) {
pro.checked = false;
pro.textColor = false;
if(this.getCustomerArea.province != ''){
if (pro.label === this.getCustomerArea.province) {
pro.checked = true
}
}
}
this.provinceList = areajson
},
data() {
return {
showAreaList: true,
provinceList: [{
checked: false
}],
province: '',
showProvince: true,
cityList: [],
city: '',
showCity: false,
areaList: [],
area: '',
showArea: false
}
},
computed: {
...mapGetters([
'getCustomerArea'
])
},
methods: {
...mapMutations({
setCusmoterArea: 'SET_CUSTOMERAREA'
}),
change() {
console.log('change')
},
back() {
if (this.showArea === true) {
this.showCity = true
this.showArea = false
this.showProvince = false
} else if (this.showCity === true) {
this.showCity = false
this.showProvince = true
this.showArea = false
} else if (this.showProvince === true) {
this.$router.back()
}
},

goAreaList(index, name) {

this.city = name;
this.showProvince = false
this.showCity = false
this.showArea = true
this.areaList = this.cityList[index].children;
this.areaList.map(area =>{
area.checked = false;
if (this.getCustomerArea.area !== '') {
if (this.getCustomerArea.area === area.label) {
area.checked = true
}
}
});

},
selectArea(index, name) {

for (let i in this.areaList) {
this.areaList[i].checked = false
}
this.areaList[index].checked = true
this.area = name
this.setCusmoterArea({province: this.province, city: this.city, area: this.area})
this.$router.back()
},

goCityList(index, name) {

this.province = name
this.showProvince = false
this.showCity = true
this.showArea = false
this.cityList = this.provinceList[index].children;
this.cityList.map(city=>{
city.checked = false;
if(this.getCustomerArea.city !== ''){
if (this.getCustomerArea.city === city.label) {
city.checked = true
}
}
});
},

selectCity(index, name) {

for (let i in this.cityList) {
this.cityList[i].checked = false
}
this.cityList[index].checked = true
this.city = name
this.setCusmoterArea({province: this.province, city: this.city, area: this.area})
this.$router.back()
},

selectProvince(index, name) {
this.provinceList.map(item=>{
item.checked = false;
})
this.provinceList[index].checked = true
this.province = name
this.setCusmoterArea({province: this.province, city: this.city, area: this.area})
this.$router.back()
},
searchGetFocus() {
},
searchLoseFocus() {
}
},
components: {
XHeader,
Search,
Checklist,
CheckIcon
}
}



